import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom';
import CssBaseline from '@material-ui/core/CssBaseline';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import { Helmet } from "react-helmet";

import HomeContainer from './containers/HomeContainer';
import ResponderCataContainer from './containers/ResponderCataContainer';
import ResultadosCataContainer from './containers/ResultadosCataContainer';
import CrearCataContainer from './containers/CrearCataContainer';
import CatasUsuarioContainer from './containers/CatasUsuarioContainer';
import AcercaDeContainer from './containers/AcercaDeContainer';


const theme = createMuiTheme({
  palette: {
    primary: {      
      main: '#9c27b0',            
    },
    secondary: {      
      main: '#e040fb',      
    },
    typography: {
      useNextVariants: true,
    },
  },
});

class SeisPreguntasApp
 extends Component {

  render() {
    return (
      <MuiThemeProvider theme={theme}>   
        
        <Helmet>          
          <title>6preguntas App</title>                    
        </Helmet>   
        
        <CssBaseline />
        
        <Switch>                                                 
          <Route exact path="/6preguntas/cata/:id" component={ResponderCataContainer} />
          <Route exact path="/6preguntas/cata/:id/resultados" component={ResultadosCataContainer} />          
          <Route exact path="/6preguntas/crear" component={CrearCataContainer} />
          <Route exact path="/6preguntas/miscatas" component={CatasUsuarioContainer} />
          <Route exact path="/6preguntas/acercade" component={AcercaDeContainer} />
          <Route path="/6preguntas/" component={HomeContainer} />
        </Switch>        

      </MuiThemeProvider>
    );
  }
}

export default SeisPreguntasApp;
